import styles from "./order-details.module.css";
import acceptImgPath from "../../images/accept-order.svg";
import { useAppSelector } from "../../utils/hooks";

export default function OrderDetails() {
    const orderId = useAppSelector((state) => state.order.id);
    return (
        <section>
            <article className={`${styles.orderDetails} mb-20 mt-4`}>
                <p
                    className={`${styles.order} text text_type_digits-large mb-8`}
                    data-cy="orderId"
                >
                    {orderId}
                </p>
                <p className="text text_type_main-medium mb-15">
                     
                </p>
                <img src={acceptImgPath} className="mb-15" alt="" />
                <p className="text text_type_main-default mb-2">
                       
                </p>
                <p className="text text_type_main-default text_color_inactive">
                        
                </p>
            </article>
        </section>
    );
}
